*{margin: 0;padding: 0;}
.app-wraper{
  display: grid; 
  width: 80%;
  
  margin: 0 auto;
  grid-template: 
  'h h'
  'n c';
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 5em 1fr;
  grid-column-gap: 0.7em;
  grid-row-gap: 0.7em;
}
.fSpaceBetween{
  justify-content: space-between;
}
.b{
  border:1px solid red;
}
.flex{
  display: flex;
}
.flex1{
  flex:1;
}
.flex2{
  flex: 2;
}
.p1{
  padding: 1em;
}
.block{
  display: block;
}
.content{
  background: rgba(0, 0, 0, 0.068);
}